import 'react-native-gesture-handler';
import { Animated, Easing } from 'react-native';
import React, { useState, useEffect } from 'react';
import GestureRecognizer from 'react-native-swipe-gestures';
import {widthPercentageToDP as wp, heightPercentageToDP as hp} from 'react-native-responsive-screen';

import {Scrollable, HomeWrapper, HomeColumn, FlexRow, HomeMessageText} from './components'
import Moviecard from '../components/movie-card/MovieCard'
import Menu from './Menu';

const Home = ({RootNavigation, movies, filterMovies}:any)=> {
  const [moviesDisplayed, setMoviesDisplayed] = useState(movies)
  const [loading, setLoading] = useState(false)
  const [menuOpen, setMenuOpen] = useState(0)
  const [filter, setFilter]=useState({
    series: -1,
    seen: false,
    genres:{
        COMEDY: false,
        HORROR: false,
        THRILLER: false,
        WESTERN: false,
        FANTASY: false,
        DRAMA: false,
        DOCUMENTARY: false,
      }
    })
  const [slideAnim, setSlideAnim] = useState(new Animated.Value(menuOpen ? -wp('50%')+4 : 0))
    
  const columnNumber = wp('100%') <= 576 ? 2 : wp('100%') <= 768 ? 3 : Math.floor(wp('100%')/300)
  // let slideAnim = new Animated.Value(menuOpen ? -wp('50%')+8 : 0);
  

  useEffect(()=>{
    setLoading(true);
    const moviesToBeDisplayed = filterMovies(filter);
    setMoviesDisplayed(moviesToBeDisplayed);
    setLoading(false);
  }, [filter])

  useEffect(()=>{
    if(!menuOpen){setSlideAnim(new Animated.Value(-wp('50%')+4));return}
    Animated.timing(slideAnim, {
      toValue: !menuOpen ? -wp('50%')+4 : 0,
      duration: 500,
      easing: Easing.bezier(0.34,0.86,0.46,0.91)
    }).start();
  }, [menuOpen])


  const openMenu = () => {
    if(menuOpen){return}
    setMenuOpen(1);
  }

  const closeMenu = () => {
    if(!menuOpen){RootNavigation.navigate('AddMovie'); return}
    setMenuOpen(0);
  }


  return (
    <HomeWrapper>
      {console.log(slideAnim)}
      <Animated.View style={{marginLeft: slideAnim}}>
        <Menu navigation={RootNavigation} filter={filter} setFilter={setFilter} openMenu={openMenu} openStatus={menuOpen}/>
      </Animated.View> 
      <Scrollable>
        <GestureRecognizer
            onSwipeRight={openMenu}
            onSwipeLeft={closeMenu}
            config={{
              velocityThreshold: 0.3,
              directionalOffsetThreshold: 80
            }}
            style={{
              flex: 1,
              minHeight: hp('100%'),
            }}>
          <FlexRow style={{justifyContent: menuOpen ? 'flex-start' :'center'}}>
            {loading && <HomeMessageText>Loading...</HomeMessageText> ||
              (!moviesDisplayed.length && <HomeMessageText>No Movies Found</HomeMessageText>) ||
                (Array.from({length: columnNumber-menuOpen}, (v, i) => i)).map((col)=>
                  <HomeColumn>
                    {moviesDisplayed && moviesDisplayed.map((movie:any, i:number)=>
                      <>
                        {i%(columnNumber-menuOpen)==col &&
                          <Moviecard movie={movie} updateMovie={(movie:any)=>console.log(movie, i)}/>
                        }
                      </>
                    )}
                  </HomeColumn>
              )}
          </FlexRow>
        </GestureRecognizer>
      </Scrollable>
    </HomeWrapper>
  );
}

export default Home
