import React,{ FunctionComponent, useState } from 'react';
import FitImage from 'react-native-fit-image';
import {EditingWrapper, DefaultText, EditLayerButton, CheckmarkIcon,EditLayerTouchable } from './components'
import { TouchableOpacity } from "react-native";
import CheckmarkWhite from '../../assets/checkmark-white.png'



const EditingLayer:FunctionComponent<any> = ({movie, cancelHandle, updateMovie}) => {
    return (
      <EditingWrapper>
        <EditLayerTouchable>
        <EditLayerButton onTouchEnd={()=>updateMovie({...movie, seen: !movie.seen})}>
          <DefaultText>Watched</DefaultText>
          {movie.seen &&
          <CheckmarkIcon source={CheckmarkWhite} />
           }
        </EditLayerButton>
        </EditLayerTouchable>
        <EditLayerTouchable>
        <EditLayerButton >
          <DefaultText>Edit</DefaultText>
        </EditLayerButton>
        </EditLayerTouchable>
            <EditLayerTouchable>
        <EditLayerButton onTouchEnd={cancelHandle}>
          <DefaultText >Cancel</DefaultText>
        </EditLayerButton>
      </EditLayerTouchable>
      </EditingWrapper>
      )
}


export default EditingLayer