import {AsyncStorage} from 'react-native';


export const setItem = async(key:string, val:any)=>{
    await AsyncStorage.setItem(key, JSON.stringify(val))
} 

export const getItem = async(key:string, callBack?:any):Promise<any>=>{
    await AsyncStorage.getItem(key,(err,res)=>{callBack && callBack(err, res)})
} 

export const initializeMovies = async()=>{
    const currMovies = await getItem('storageMovies');
    if(!!currMovies.length){return}
    await setItem('storageMovies',[]);
}
export const addMovie = async(movie:any)=>{
    const currMovies = await getItem('storageMovies');
    if(currMovies && currMovies.length){
        setItem('storageMovies', [movie,...currMovies]);
    }else{
        setItem('storageMovies', [movie]);
    }
}



