import {View, TouchableOpacity, ScrollView, Keyboard} from "react-native";
import {widthPercentageToDP as wp, heightPercentageToDP as hp} from 'react-native-responsive-screen';
import React, { useState, useEffect } from 'react';
import FitImage from 'react-native-fit-image';
import 'react-native-gesture-handler';
import axios from 'axios';

import {AddMovieWrapper, AddMovieTextInput, FlexRow, AddMovieSearchButton, AddMovieCard, AddMovieCardPoster, AddMovieCardTitle, AddMovieBlurredSquare, AddMovieSelectGenresRow, AddMovieGenre, AddMovieButton, AddMovieButtonIcon, AddMovieSearchWrapper, AddMovieSearchIcon, AddMovieSearchResultText,AddMovieSearchInputWrapper, AddMovieCardButton, AddMovieSearchCardWrapper, AddMovieCardButtonText} from './components'

import SearchBlack from '../assets/search-black.png'
import CheckmarkBlack from '../assets/checkmark-black.png'
import EyeBlack from '../assets/eye-black.png'
import XBlack from '../assets/x-black.png'
import { TouchableWithoutFeedback } from "react-native-gesture-handler";

const MoviePropsInitial = {
  seen: false,
  COMEDY: false,
  HORROR: false,
  THRILLER: false,
  WESTERN: false,
  FANTASY: false,
  DRAMA: false,
  DOCUMENTARY: false,
}

const AddMovie = ({navigation, setMovies,checkMovieHave}:any)=> {
    const [movieProperties, setMovieProperties]=useState(MoviePropsInitial);
    const [movieToBeAdded, setMovieToBeAdded] = useState(null)
    const [loading, setLoading] = useState(false)
    const [typing, setTyping] = useState(false)
    const [result, setResult] = useState([]);
    const [search, setSearch] = useState('');

    const searchhandler = async () =>{
      Keyboard.dismiss();
      setTyping(false);
      if(!search.trim()){return;}
      setLoading(true);
      const result = await axios.get(`http://www.omdbapi.com/?s=${search.trim()}&apikey=976eb399`);
      if(result && result.data && result.data.Search){
        setResult(result.data.Search);
      }else{setResult([])}
      setLoading(false)
    }
    const submitMovieHandler = async () =>{
      const {seen, ...rest} = movieProperties;
      setMovieProperties(MoviePropsInitial); 
      setMovies((curr:any)=>[{...movieToBeAdded, seen: seen, genres: rest}, ...curr]); 
      navigation.navigate('Home');
      setMovieToBeAdded(null)
    }

    return (
      <AddMovieWrapper >
          {movieToBeAdded && 
          <>
            <FitImage source={{uri: movieToBeAdded.cover}} /> 
            <AddMovieBlurredSquare  style={{  shadowColor: "#101016",
                                              shadowOffset:{
                                                width: -60,
                                                height: -57,
                                              },
                                              shadowOpacity: 1,
                                              shadowRadius: 27,
                                              elevation: 0,}}
            />
            <AddMovieBlurredSquare  style={{  shadowColor: "#101016",
                                              shadowOffset:{
                                                width: 60,
                                                height: -57,
                                              },
                                              shadowOpacity: 1,
                                              shadowRadius: 27,
                                              elevation: 0,}}
            >
              <View>
                <AddMovieSelectGenresRow>
                  <AddMovieGenre selected={movieProperties.COMEDY} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, COMEDY: !curr.COMEDY}))}>COMEDY</AddMovieGenre>
                  <AddMovieGenre selected={movieProperties.HORROR} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, HORROR: !curr.HORROR}))}>HORROR</AddMovieGenre>
                  <AddMovieGenre selected={movieProperties.DRAMA} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, DRAMA: !curr.DRAMA}))}>DRAMA</AddMovieGenre>
                  <AddMovieGenre selected={movieProperties.WESTERN} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, WESTERN: !curr.WESTERN}))}>WESTERN</AddMovieGenre>
                </AddMovieSelectGenresRow>
                <AddMovieSelectGenresRow>
                  <AddMovieGenre selected={movieProperties.THRILLER} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, THRILLER: !curr.THRILLER}))}>THRILLER</AddMovieGenre>
                  <AddMovieGenre selected={movieProperties.DOCUMENTARY} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, DOCUMENTARY: !curr.DOCUMENTARY}))}>DOCUMENTARY</AddMovieGenre>
                </AddMovieSelectGenresRow>
                <AddMovieSelectGenresRow>
                  <AddMovieGenre selected={movieProperties.FANTASY} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, FANTASY: !curr.FANTASY}))}>FANTASY</AddMovieGenre>
                </AddMovieSelectGenresRow>
              </View>
              <View>
                <AddMovieButton selected={movieProperties.seen} onTouchEnd={()=>setMovieProperties((curr:any)=>({...curr, seen: !curr.seen}))}>
                  <AddMovieButtonIcon source={EyeBlack}/>
                </AddMovieButton>
                <FlexRow>
                  <TouchableOpacity>
                    <AddMovieButton>
                      <AddMovieButtonIcon source={XBlack} onTouchEnd={()=>{setMovieProperties(MoviePropsInitial); 
                        // setSearch(''); setResult([]);
                        setMovieToBeAdded(null)}}/>
                    </AddMovieButton>
                  </TouchableOpacity>
                  <TouchableOpacity>
                    <AddMovieButton>
                      <AddMovieButtonIcon source={CheckmarkBlack} onTouchEnd={submitMovieHandler}/>
                    </AddMovieButton>
                  </TouchableOpacity>
                </FlexRow>
              </View>
            </AddMovieBlurredSquare>
          </>
            ||
          <>
        <TouchableWithoutFeedback onPress={Keyboard.dismiss}>
            <AddMovieSearchWrapper typing={typing}>

              <AddMovieSearchInputWrapper >
                <AddMovieTextInput  onChangeText={(text:string)=> setSearch(text)} 
                                    selectionColor={'#101021'} placeholder='Enter Title' 
                                    placeholderTextColor='#ffffff7d' value={search}
                                    keyboardAppearance='dark'
                                    returnKeyLabel='Search'
                                    selectTextOnFocus={true}
                                    spellCheck={true}
                                    onBlur={searchhandler} 
                                    onFocus={()=>setTyping(true)} />
                <AddMovieSearchButton onTouchEnd={searchhandler} onPress={searchhandler} faded={!search.trim()}>
                  <AddMovieSearchIcon source={SearchBlack} />
                </AddMovieSearchButton>
              </AddMovieSearchInputWrapper>
              {
                (loading && <AddMovieSearchResultText>LOADING...</AddMovieSearchResultText>) ||
                (result.length &&
                  <AddMovieSearchCardWrapper style={{padding: 8, flexGrow: 1}}>
                    <ScrollView>
                      {result.map((movie:any)=>{
                        const having = checkMovieHave(movie.imdbID)
                        return(
                          <AddMovieCard>
                            <AddMovieCardPoster source={{uri: movie.Poster}}/>
                            <AddMovieCardTitle numberOfLines={1}>{movie.Title}</AddMovieCardTitle>
                            <AddMovieCardButton>
                              <AddMovieCardButtonText disabled={having} onTouchEnd={()=>!having && setMovieToBeAdded({title: movie.Title, cover: movie.Poster, id: movie.imdbID, liked: false, series: movie.Type==='series'})}>{having&&'REM'||'ADD'}</AddMovieCardButtonText>
                            </AddMovieCardButton>
                          </AddMovieCard>
                        ) 
                      })}
                    </ScrollView>
                  </AddMovieSearchCardWrapper>
                ) ||
                <AddMovieSearchResultText>ADD YOUR MOVIE OR TVS</AddMovieSearchResultText>
              }
            </AddMovieSearchWrapper>
            </TouchableWithoutFeedback>

          </>
          }
            </AddMovieWrapper> 

  );
}

export default AddMovie