import React from 'react';
import 'react-native-gesture-handler';
import { TouchableOpacity, View, Linking} from "react-native";
import {widthPercentageToDP as wp, heightPercentageToDP as hp} from 'react-native-responsive-screen';

import {MenuTypeButton,MenuListItem, MenuWrapper, FlexRow,MenuViewButton, MenuPlusCallButton, MenuButtonIcon,MenuContentWrapper, MenuOpener, MenuOpenerIcon} from './components';

import TriangleRightYellow from '../assets/triangle-right-yellow.png'
import TicketsBlack from '../assets/tickets-black.png'
import PhoneBlack from '../assets/phone-black.png'
import PlusBlack from '../assets/plus-black.png'
import EyeBlack from '../assets/eye-black.png'
import TvsBlack from '../assets/tvs-black.png'



const Menu = ({navigation, filter, setFilter, openMenu, openStatus}:any)=> {
    return (
            <MenuWrapper width={wp('50%')+4}>
                {!openStatus &&
                <MenuOpener onTouchStart={openMenu} onClick={openMenu}>
                    <MenuOpenerIcon source={TriangleRightYellow}/>
                </MenuOpener>
                }
                <MenuContentWrapper>
                    <View>
                        <FlexRow>
                            <MenuTypeButton
                                selected={filter.series===1} onTouchEnd={()=>setFilter((curr:any)=>({...curr, series: filter.series==1 ? -1 : 1}))}>
                                <MenuButtonIcon big source={TvsBlack}  />
                            </MenuTypeButton>
                            <MenuTypeButton
                                selected={filter.series===0} onTouchEnd={()=>setFilter((curr:any)=>({...curr, series: filter.series==0 ? -1 : 0}))}>
                                <MenuButtonIcon big source={TicketsBlack} />
                            </MenuTypeButton>
                        </FlexRow>
                        <MenuViewButton
                            selected={filter.seen} onTouchEnd={()=>setFilter((curr:any)=>({...curr, seen: !filter.seen}))}>
                            <MenuButtonIcon source={EyeBlack} />
                        </MenuViewButton>
                    </View>
                    <View>
                        <MenuListItem 
                            selected={filter.genres.COMEDY} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, COMEDY: !filter.genres.COMEDY}}))}>
                            COMEDY
                        </MenuListItem>
                        <MenuListItem 
                            selected={filter.genres.HORROR} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, HORROR: !filter.genres.HORROR}}))}>
                            HORROR
                        </MenuListItem>
                        <MenuListItem 
                            selected={filter.genres.THRILLER} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, THRILLER: !filter.genres.THRILLER}}))}>
                            THRILLER
                        </MenuListItem>
                        <MenuListItem 
                            selected={filter.genres.WESTERN} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, WESTERN: !filter.genres.WESTERN}}))}>
                            WESTERN
                        </MenuListItem>
                        <MenuListItem 
                            selected={filter.genres.FANTASY} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, FANTASY: !filter.genres.FANTASY}}))}>
                            FANTASY
                        </MenuListItem>
                        <MenuListItem 
                            selected={filter.genres.DRAMA} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, DRAMA: !filter.genres.DRAMA}}))}>
                            DRAMA
                        </MenuListItem>
                        <MenuListItem 
                            selected={filter.genres.DOCUMENTARY} onTouchEnd={()=>setFilter((curr:any)=>({...curr, genres: {...curr.genres, DOCUMENTARY: !filter.genres.DOCUMENTARY}}))}>
                            DOCUMENTARY
                        </MenuListItem>
                    </View>
                    <FlexRow>
                        <TouchableOpacity>
                            <MenuPlusCallButton onTouchEnd={()=>navigation.navigate('AddMovie')}>
                                <MenuButtonIcon small source={PlusBlack}/>
                            </MenuPlusCallButton>
                        </TouchableOpacity>
                        <TouchableOpacity>
                            <MenuPlusCallButton onTouchEnd={()=>Linking.openURL(`tel:${+995557773417}`)}>
                                <MenuButtonIcon small source={PhoneBlack}/>
                            </MenuPlusCallButton>
                        </TouchableOpacity>
                    </FlexRow>
                </MenuContentWrapper>
            </MenuWrapper>
  );
}

export default Menu