import 'react-native-gesture-handler';
import React, { useState, useEffect } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { createDrawerNavigator } from '@react-navigation/drawer';
import Home from './screens/Home'
import AddMovie from './screens/AddMovie';
import {setItem,getItem, initializeMovies,addMovie} from './db'
import Menu from './screens/Menu';
import { Animated } from 'react-native';
import {widthPercentageToDP as wp, heightPercentageToDP as hp} from 'react-native-responsive-screen';



const Stack = createStackNavigator();



const defaultScreenOptions={
  headerShown: false,
  cardStyle:{
    backgroundColor: 'transparent'
  },
};


export default function App() {
  // initializeMovies();
  const [movies, setMovies] = useState([])

  useEffect(()=>{
    getItem('storageMovies', (err:any, res:any)=>setMovies(JSON.parse(res)||[]));
  }, [])

  useEffect(()=>{
    if(!movies || !movies.length){return}
    setItem('storageMovies', movies);
  }, [movies.length])
  
  const filterMovies = (filter:any) => {
    const {genres, series, seen} = filter;
    const genreArray = Object.keys(genres).filter(x=>genres[x])
    return movies.filter((x:any)=>
      (!genreArray.length || genreArray.find((y:string)=>x.genres && x.genres[y]))
        &&
      (series<0 || x.series === !!series)
      &&
      (!seen || x.seen)
      )
    }

  const checkMovieHave = (id:string) => {
    return !!movies.find((x:any)=>x.id === id);
  }
  
  const AddMovieScreen = ({navigation}:any) =>{
    return(
      <AddMovie setMovies={setMovies} navigation={navigation} checkMovieHave={checkMovieHave}/>
      )
    }

  const HomeScreen = ({navigation}:any) =>{
    return(
      <Home movies={movies} RootNavigation={navigation} filterMovies={filterMovies} />
    )
  }


  return (
    <NavigationContainer>
    <Stack.Navigator initialRouteName='Home' screenOptions={defaultScreenOptions}>
      {/* <Stack.Screen name="Menu" component={Menu} options={{gestureDirection: 'horizontal-inverted'}}/> */}
      <Stack.Screen name="Home" component={HomeScreen} options={{gestureDirection: 'horizontal'}}/>
      <Stack.Screen name="AddMovie" component={AddMovieScreen} options={{gestureDirection: 'horizontal'}} />
    </Stack.Navigator>    
    </NavigationContainer>
  )
}


